3.event </p>
<h2 id="xian-shi-xia-zai-lian-jie"><a class="header-anchor" href="#xian-shi-xia-zai-lian-jie" aria-hidden="true"></a> </h2>
<p> fileList  url </p>
</undefined></template>

<script>
  export default {
    data: ()=> ({
      name: 'file',
      action: '/upload',
      header:{
        appkey: 'FDF8F7DSFSD8FSA987A8SF',
      },
      defaultFileList: [{
        uid: -1,
        name: 'xxx.png',
        status: 'done',
        url: 'http://www.baidu.com/xxx.png'
      }, {
        uid: -2,
        name: 'yyy.png',
        status: 'done',
        url: 'http://www.baidu.com/yyy.png'
      }]
    }),
    methods: {
      onChange(info) {
        if (info.file.status !== 'uploading') {
          console.log(info.file, info.fileList)
        }
        if (info.file.status === 'done') {
          console.log(info.file.name + ' .')
        } else if (info.file.status === 'error') {
          console.log(info.file.name + ' .')
        }
      }
    }
  }
</script>


